"use client";
import React, { useState } from "react";
import Image from "next/image";
import Header from "@/component/Header";
import Footer from "@/component/Footer";
import useGlobalContext from "@/context/globalContext";
import { Mail, MapPin, Calendar, Github, Linkedin, Twitter, Globe, Edit3, Award, BookOpen, Target, Trophy } from "lucide-react";
import { Progress } from "@/components/ui/progress"; // Ensure you have this shadcn component

export default function ProfilePage() {
  const { userProfile, isAuthenticated } = useGlobalContext();
  const [activeTab, setActiveTab] = useState("overview");

  if (!isAuthenticated) return (
    <div className="min-h-screen flex items-center justify-center">
      <p className="text-xl font-bold text-gray-500">Please login to view your profile.</p>
    </div>
  );

  const stats = [
    { label: "Competitions", value: "24", icon: <Trophy className="w-5 h-5 text-blue-500" /> },
    { label: "Achievements", value: "12", icon: <Award className="w-5 h-5 text-purple-500" /> },
    { label: "Courses", value: "8", icon: <BookOpen className="w-5 h-5 text-indigo-500" /> },
    { label: "Rank Points", value: "1,245", icon: <Target className="w-5 h-5 text-green-500" /> },
  ];

  return (
    <main className="bg-gray-50 min-h-screen">
      <Header />
      
      {/* Profile Header Banner */}
      <div className="bg-gradient-to-r from-purple-100 to-indigo-50 pt-20 pb-12">
        <div className="container mx-auto px-4 md:px-8">
          <div className="flex flex-col lg:flex-row items-start gap-8">
            
            {/* Left: Avatar & Edit */}
            <div className="relative group">
              <div className="w-32 h-32 md:w-40 md:h-40 rounded-full border-4 border-white shadow-lg overflow-hidden bg-white">
                <Image 
                  src={userProfile?.profilePicture || "/user.png"} 
                  alt="Profile" 
                  width={160} 
                  height={160} 
                  className="object-cover"
                />
              </div>
              <button className="mt-4 flex items-center gap-2 px-4 py-2 bg-white border border-gray-200 rounded-lg text-sm font-bold shadow-sm hover:bg-gray-50 transition-all w-full justify-center">
                <Edit3 size={16} /> Edit Profile
              </button>
            </div>

            {/* Middle: User Info */}
            <div className="flex-1">
              <div className="flex items-center gap-3 mb-2">
                <h1 className="text-3xl font-bold text-gray-900">{userProfile?.name || "John Doe"}</h1>
                <span className="bg-purple-600 text-white text-[10px] font-bold px-2 py-1 rounded-full uppercase tracking-wider">Pro Member</span>
              </div>
              <p className="text-lg text-gray-600 font-medium mb-4">Full Stack Developer | AI Enthusiast | Competitive Programmer</p>
              
              <div className="flex flex-wrap gap-4 text-sm text-gray-500 mb-6">
                <span className="flex items-center gap-1"><MapPin size={16} /> Mumbai, India</span>
                <span className="flex items-center gap-1"><Mail size={16} /> {userProfile?.email}</span>
                <span className="flex items-center gap-1"><Calendar size={16} /> Joined Jan 2023</span>
              </div>

              <div className="flex gap-3">
                {[Github, Linkedin, Twitter, Globe].map((Icon, i) => (
                  <button key={i} className="p-2 border border-gray-200 rounded-lg bg-white hover:bg-gray-50 text-gray-600">
                    <Icon size={18} />
                  </button>
                ))}
              </div>
            </div>

            {/* Right: Quick Stats Grid */}
            <div className="grid grid-cols-2 gap-4 w-full lg:w-auto">
              {stats.map((stat, i) => (
                <div key={i} className="bg-white p-4 rounded-xl shadow-sm border border-gray-100 flex flex-col items-center justify-center min-w-[120px]">
                  {stat.icon}
                  <span className="text-xl font-bold text-gray-900 mt-1">{stat.value}</span>
                  <span className="text-xs text-gray-500 font-medium">{stat.label}</span>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Tabs Navigation */}
      <div className="bg-white border-b border-gray-200 sticky top-0 z-20">
        <div className="container mx-auto px-4 md:px-8">
          <div className="flex gap-8 overflow-x-auto no-scrollbar">
            {["Overview", "Participations", "Achievements", "Skills"].map((tab) => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab.toLowerCase())}
                className={`py-4 px-2 text-sm font-bold border-b-2 transition-all ${
                  activeTab === tab.toLowerCase() ? "border-purple-600 text-purple-600" : "border-transparent text-gray-500 hover:text-gray-700"
                }`}
              >
                {tab}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Dynamic Content Section */}
      <div className="container mx-auto px-4 md:px-8 py-10">
        {activeTab === "overview" && <OverviewSection />}
        {activeTab === "skills" && <SkillsSection />}
        {/* You can add more conditional components here */}
      </div>

      <Footer />
    </main>
  );
}

// Sub-component for the Overview Tab
function OverviewSection() {
  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
      <div className="lg:col-span-2 space-y-8">
        <div className="bg-white p-6 rounded-xl border border-gray-100 shadow-sm">
          <h3 className="text-xl font-bold mb-4">About</h3>
          <p className="text-gray-600 leading-relaxed">
            Passionate full-stack developer with 3+ years of experience in building web applications. 
            Love participating in hackathons and competitive programming. Always eager to learn new technologies and solve complex problems.
          </p>
          <div className="mt-6">
            <h4 className="font-bold text-sm text-gray-500 mb-3 uppercase tracking-wider">Interests</h4>
            <div className="flex flex-wrap gap-2">
              {["AI/ML", "Web Development", "Cloud Computing", "Mobile Apps", "Blockchain"].map(tag => (
                <span key={tag} className="px-3 py-1 bg-gray-100 rounded-lg text-sm text-gray-700">{tag}</span>
              ))}
            </div>
          </div>
        </div>
      </div>
      
      <div className="space-y-6">
        <div className="bg-white p-6 rounded-xl border border-gray-100 shadow-sm">
          <h3 className="font-bold text-lg mb-4">Recent Wins</h3>
          <div className="space-y-4">
            {[
              { title: "AI Challenge Winner", rank: "1st Place", date: "Jan 2024", color: "text-yellow-500" },
              { title: "Web Dev Sprint", rank: "2nd Place", date: "Dec 2023", color: "text-gray-400" },
              { title: "Design Battle", rank: "3rd Place", date: "Nov 2023", color: "text-orange-400" },
            ].map((win, i) => (
              <div key={i} className="flex items-center justify-between group cursor-pointer">
                <div className="flex items-center gap-3">
                  <div className={`p-2 rounded-lg bg-gray-50 ${win.color}`}><Trophy size={18} /></div>
                  <div>
                    <p className="font-bold text-sm text-gray-900">{win.title}</p>
                    <p className="text-xs text-gray-500">{win.rank}</p>
                  </div>
                </div>
                <span className="text-[10px] text-gray-400">{win.date}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

// Sub-component for the Skills Tab
function SkillsSection() {
  const skills = [
    { name: "JavaScript", value: 90 },
    { name: "React", value: 85 },
    { name: "Python", value: 80 },
    { name: "UI/UX Design", value: 75 },
    { name: "Node.js", value: 70 },
    { name: "Machine Learning", value: 65 },
  ];

  return (
    <div className="bg-white p-8 rounded-xl border border-gray-100 shadow-sm max-w-4xl mx-auto">
      <h3 className="text-xl font-bold mb-8">Skill Proficiency</h3>
      <div className="space-y-8">
        {skills.map((skill) => (
          <div key={skill.name}>
            <div className="flex justify-between mb-2">
              <span className="font-bold text-gray-700">{skill.name}</span>
              <span className="text-sm font-bold text-gray-500">{skill.value}%</span>
            </div>
            <div className="h-2 w-full bg-indigo-50 rounded-full overflow-hidden">
               <div 
                 className="h-full bg-purple-600 transition-all duration-1000" 
                 style={{ width: `${skill.value}%` }} 
               />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

function ParticipationsSection() {
  const participations = [
    { title: "AI Innovation Challenge 2024", status: "Ongoing", rank: "Top 10", img: "/ai-challenge.jpg" },
    { title: "Full Stack Development Bootcamp", status: "Completed", rank: "Certified", img: "/bootcamp.jpg" },
    { title: "Design Sprint Competition", status: "Ongoing", rank: "Top 25", img: "/design-sprint.jpg" },
  ];

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {participations.map((item, i) => (
        <div key={i} className="bg-white rounded-xl overflow-hidden border border-gray-100 shadow-sm group">
          <div className="h-40 bg-gray-200 relative overflow-hidden">
            {/* Replace with real images later */}
            <div className="absolute inset-0 bg-gradient-to-br from-indigo-500/20 to-purple-500/20" />
            <div className="absolute inset-0 flex items-center justify-center text-gray-400 font-bold uppercase tracking-tighter text-4xl opacity-20">Challenge</div>
          </div>
          <div className="p-5">
            <h4 className="font-bold text-gray-900 mb-4 group-hover:text-purple-600 transition-colors">{item.title}</h4>
            <div className="flex justify-between items-center">
              <span className={`px-3 py-1 rounded-full text-[10px] font-bold uppercase ${
                item.status === "Ongoing" ? "bg-blue-100 text-blue-600" : "bg-gray-100 text-gray-600"
              }`}>
                {item.status}
              </span>
              <span className="text-xs font-bold text-indigo-500">{item.rank}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}